form {
    width: 90%;
    max-width: 300px;
    background-color: #fff;
    padding: 33px 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

label{
    text-align: center;
    font-weight: bold;
    font-size: 20px;
    color: black;
    margin-bottom: 26px;
}

textarea {
background-color: #F9FBFC;
border: 2px solid #202329;
box-shadow: 0px 5px 50px rgba(32, 33, 41, 0.25);
color: #1e1e1f;
text-align: center;
font-size: 20px;
padding: 12px;
height: 96px;
width: calc(100% - 25px);
}

textarea::placeholder{
    color: #a5a5a5;
    font-family: 'Courier New', Courier, monospace;
    font-weight: 400;
}

textarea:focus{
    outline-color: #61DAFA;
}

.TodoForm-buttonContainer {
    margin-top: 14px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
}

.TodoForm-button {
    cursor: pointer;
    display: inline-block;
    font-size: 20px;
    color: #202329;
    font-weight: 400;
    width: 120px;
    height: 48px;
    border-radius: 2px;
    border: none;
    font-family: 'Courier New', Courier, monospace;
}

.TodoForm-button--add {
    background-color: #61DAFA;
    box-shadow: 0px 5px 25px rgba(97, 218, 250, 0.5);
}

.TodoForm-button--cancel {
    background: transparent;
}