.TodoItem{
    background-color: #FAFAFA;
    position: relative;
    display: flex;
    justify-content: center;
    margin-top: 24px;
    box-shadow: 0px 5px 50px ;
}

.TodoItem-p{
    margin: 24px 0 24px 24px;
    width: calc(100% - 120px);
    font-size: 18px;
    line-height: 24px;
    font-weight: 400;
}

.TodoItem-p--complete{
    text-decoration: line-through;
}

.Icon{
    cursor:pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 48px;
    width: 48px;
    font-size: 24px;
}

.Icon-check{
    position: absolute;
    left: 12px;
    top: 10px;
    
}
.Icon-check:hover{
    color: green
}

.Icon-check--active{
    color: darkseagreen;
}

.Icon-delete {
    position: absolute;
    top: 10px;
    right: 0;
}

.Icon-delete:hover{
    color: red;
}
